import { Component, Directive, Input, Renderer2, ElementRef, AfterViewInit, ViewContainerRef, TemplateRef, ViewChild,
         HostBinding, HostListener, ChangeDetectorRef } from '@angular/core';

import { TdCollapseAnimation, TdFadeInOutAnimation } from '@covalent/core/common';

@Directive({
  selector: '[tdMessageContainer]',
})
export class TdMessageContainerDirective {
  constructor(public viewContainer: ViewContainerRef) { }
}

@Component({
  selector: 'td-message',
  template: `<div tdMessageContainer></div>
<ng-template>
  <div class="td-message-wrapper">
    <mat-icon class="td-message-icon">{{icon}}</mat-icon>
    <div class="td-message-labels">
      <div *ngIf="label" class="td-message-label">{{label}}</div>
      <div *ngIf="sublabel" class="td-message-sublabel">{{sublabel}}</div>
    </div>
    <ng-content select="[td-message-actions]"></ng-content>
  </div>
</ng-template>`,
  styles: [`:host {
  display: block; }
  :host .td-message-wrapper {
    padding: 8px 16px;
    min-height: 52px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -ms-flex-line-pack: center;
        align-content: center;
    max-width: 100%;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: start; }
    :host .td-message-wrapper .td-message-labels {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1; }

.td-message-icon {
  margin-right: 16px; }
  ::ng-deep [dir='rtl'] .td-message-icon {
    margin-left: 16px;
    margin-right: 0; }
`],
  animations: [
    TdCollapseAnimation({ duration: 100 }),
  ],
})
export class TdMessageComponent implements AfterViewInit {

  private _color: string;
  private _opened: boolean = true;
  private _hidden: boolean = false;
  private _animating: boolean = false;
  private _initialized: boolean = false;

  @ViewChild(TdMessageContainerDirective) _childElement: TdMessageContainerDirective;
  @ViewChild(TemplateRef) _template: TemplateRef<any>;

  /**
   * Binding host to tdCollapse animation
   */
  @HostBinding('@tdCollapse')
  get collapsedAnimation(): boolean {
    return !this._opened;
  }

  /**
   * Binding host to display style when hidden
   */
  @HostBinding('style.display')
  get hidden(): string {
    return this._hidden ? 'none' : undefined;
  }

  /**
   * label: string
   *
   * Sets the label of the message.
   */
  @Input('label') label: string;

  /**
   * sublabel?: string
   *
   * Sets the sublabel of the message.
   */
  @Input('sublabel') sublabel: string;

  /**
   * icon?: string
   *
   * The icon to be displayed before the title.
   * Defaults to `info_outline` icon
   */
  @Input('icon') icon: string = 'info_outline';

  /**
   * color?: primary | accent | warn
   *
   * Sets the color of the message.
   * Can also use any material color: purple | light-blue, etc.
   */
  @Input('color')
  set color(color: string) {
    this._renderer.removeClass(this._elementRef.nativeElement, 'mat-' + this._color);
    this._renderer.removeClass(this._elementRef.nativeElement, 'bgc-' + this._color + '-100');
    this._renderer.removeClass(this._elementRef.nativeElement, 'tc-' + this._color + '-700');
    if (color === 'primary' || color === 'accent' || color === 'warn') {
      this._renderer.addClass(this._elementRef.nativeElement, 'mat-' + color);
    } else {
      this._renderer.addClass(this._elementRef.nativeElement, 'bgc-' + color + '-100');
      this._renderer.addClass(this._elementRef.nativeElement, 'tc-' + color + '-700');
    }
    this._color = color;
    this._changeDetectorRef.markForCheck();
  }
  get color(): string {
    return this._color;
  }

  /**
   * opened?: boolean
   *
   * Shows or hiddes the message depending on its value.
   * Defaults to 'true'.
   */
  @Input('opened')
  set opened(opened: boolean) {
    if (this._initialized) {
      if (opened) {
        this.open();
      } else {
        this.close();
      }
    } else {
      this._opened = opened;
    }
  }
  get opened(): boolean {
    return this._opened;
  }

  constructor(private _renderer: Renderer2,
              private _changeDetectorRef: ChangeDetectorRef,
              private _elementRef: ElementRef) {
    this._renderer.addClass(this._elementRef.nativeElement, 'td-message');
  }

  /**
   * Detach element when close animation is finished to set animating state to false
   * hidden state to true and detach element from DOM
   */
  @HostListener('@tdCollapse.done')
  animationDoneListener(): void {
    if (!this._opened) {
      this._hidden = true;
      this._detach();
    }
    this._animating = false;
    this._changeDetectorRef.markForCheck();
  }

  /**
   * Initializes the component and attaches the content.
   */
  ngAfterViewInit(): void {
    Promise.resolve(undefined).then(() => {
      if (this._opened) {
        this._attach();
      }
      this._initialized = true;
    });
  }

  /**
   * Renders the message on screen
   * Validates if there is an animation currently and if its already opened
   */
  open(): void {
    if (!this._opened && !this._animating) {
      this._opened = true;
      this._attach();
      this._startAnimationState();
    }
  }

  /**
   * Removes the message content from screen.
   * Validates if there is an animation currently and if its already closed
   */
  close(): void {
    if (this._opened && !this._animating) {
      this._opened = false;
      this._startAnimationState();
    }
  }

  /**
   * Toggles between open and close depending on state.
   */
  toggle(): void {
    if (this._opened) {
      this.close();
    } else {
      this.open();
    }
  }

  /**
   * Method to set the state before starting an animation
   */
  private _startAnimationState(): void {
    this._animating = true;
    this._hidden = false;
    this._changeDetectorRef.markForCheck();
  }

  /**
   * Method to attach template to DOM
   */
  private _attach(): void {
    this._childElement.viewContainer.createEmbeddedView(this._template);
    this._changeDetectorRef.markForCheck();
  }

  /**
   * Method to detach template from DOM
   */
  private _detach(): void {
    this._childElement.viewContainer.clear();
    this._changeDetectorRef.markForCheck();
  }
}
